import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			inputid: 2,
			money: '',
			moneyinput: '',
			username: '',
			account: '',
			allmoney: 0,
			payFrom: 3,
			minmoney: 10,
			banknumber: '',
			showtab1: false,
			showtab2: false,
			showtab3: false,
			showtab4: false,
		}
	},
	onLoad(options) {
		this.init();
	},
	onPullDownRefresh(){
		this.init(true)
	},
	methods: {
		init(flag) {
			uni.showLoading({title: '正在加载'})
			let userId = uni.getStorageSync('userId')
			this.showtab1 = this.showtab2 = this.showtab3 = this.showtab4 = false;
			api.httppostfx('/retailUser/detail',{'retailUserId':userId}).then((res) => {
				let respons = res.data
				if (respons.code == 200) {
					this.allmoney = respons.data.withdrawMoney
					this.minmoney = respons.data.minMoney
					console.log(this.allmoney)
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				uni.hideLoading()
			})
			api.httppostfx('/retailSet/detail',{'retailSetId':1}).then((res) => {
				let respons = res.data
				if (respons.code == 200) {
					let type = respons.data.type
					if (type.indexOf('1') !== -1) {
						this.showtab1 = true
					}
					if (type.indexOf('2') !== -1) {
						this.showtab2 = true
					}
					if (type.indexOf('3') !== -1) {
						this.showtab3 = true
					}
				    if (type.indexOf('4') !== -1) {
						this.showtab4 = true
					}
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh();
			})
		},
		withdrawbtn(id) {
			this.inputid = id;
			this.payFrom = id
		},
		allbtn() {
			this.money = this.allmoney;
			console.log(this.money)
		},
		subdrawbtn() {
			if (this.money < this.minmoney) {
				uni.showToast({
					title: `每次提现金额不能少于${this.minmoney}元`,icon: 'none'
				});
				return false;
			} else if (this.money>this.allmoney) {
				uni.showToast({
					title: '提现金额不能超过可提现余额',icon: 'none'
				});
				return false;
			}
			if(this.inputid !== 1 && (this.username == '' || this.account == '')) {
				uni.showToast({
					title: '姓名和账号不能为空哦！',icon: 'none'
				});
				return false;
			}
			let data = {
				 money:this.money,
				 payFrom:this.payFrom,
				 username:this.money,
				 account:this.account,
			 }
			 if(inputid == 4) {
				 data = {
				 	 money:this.money,
				 	 payFrom:this.payFrom,
				 	 username:this.money,
				 	 username:this.account,
				 	 banknumber: this.banknumber,
				  }
			 }
			// uni.showLoading({title:'请稍后'})
			api.httpPost('/withdraw/add',data).then((res) => {
				let respons = res.data
				if (respons.errno == 0) {
					uni.showToast({title: '提交申请成功！', icon: 'none'})
					this.init();
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				uni.hideLoading()
			})
		}
	}
}
